.login {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
  border-radius: 3px;
}

.login__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 2px solid lightgray;
  padding: 20px;
  background-color: white;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container > form > h5 {
  margin-bottom: 5px;
}

.login__container > form > input {
  height: 30px;
  margin-bottom: 1px;
  background-color: white;
  width: 98%;
}

.login__container > p {
  margin-top: 20px;
  font-size: 12px;
}

.login__signInButton {
  background: #c9ae6b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 20px;
  border-color: #a88734;
  color: rgb(44, 43, 43);
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 20px;
  border-color: rgb(243, 237, 237);
}
