.register {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.register__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
  border-radius: 3px;
}

.register__container {
  width: 400px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 10px;
  background-color: rgb(255, 254, 254);
}

.register__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
  padding: 5px;
  margin-right: 4px;
}

.register__container > form > h5 {
  margin-bottom: 5px;
  padding: 5px;
  margin-right: 4px;
}

.register__container > form > input {
  height: 20px;
  margin-bottom: 1px;
  background-color: rgb(252, 247, 247);
  width: 98%;
  padding: 5px;
  margin-right: 4px;
  box-shadow: 2px violet;
}

.register__container > p {
  margin-top: 20px;
  font-size: 12px;
  padding: 5px;
  margin-right: 4px;
}

.register__button {
  background: #c9ae6b;
  border-radius: 2px;
  width: 100%;
  height: 40px;
  border: 1px solid;
  margin-top: 20px;
  border-color: #a88734;
  color: rgb(44, 43, 43);
  padding: 5px;
  margin-right: 4px;
}
