.checkout {
  display: flex;
  padding: 20px;
  background-color: white;
  height: max-content;
}

.checkout__title {
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.checkout__left {
  width: 100%;
  margin-bottom: 5px;
  padding: 5px;
}
.banner__logo {
  margin-bottom: 10px;
  max-height: 100px;
  height: 52px;
  width: 100%;
  -webkit-filter: grayscale(50%);
  filter: grayscale(90%);
  border-radius: 5px;
  z-index: -1;
  box-shadow: 6px rosybrown;
}
