.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
}

.checkoutProduct__info {
  padding-left: 20px;
}

.checkoutProduct__info > button {
  background: #c9ae6b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734;
  color: rgb(44, 43, 43);
  padding: 10px;
}

.checkoutProduct__image {
  object-fit: contain;
  width: 200px;
  height: 220px;
}

.checkoutProduct__rating {
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 700;
}
